'use strict';

/**
* Compute an absolute value.
*
* @module @stdlib/math-base-special-fast-abs
*
* @example
* var abs = require( '@stdlib/math-base-special-fast-abs' );
*
* var v = abs( -1.0 );
* // returns 1.0
*
* v = abs( 2.0 );
* // returns 2.0
*
* v = abs( 0.0 );
* // returns 0.0
*
* v = abs( -0.0 );
* // returns -0.0
*
* v = abs( NaN );
* // returns NaN
*/

// MODULES //

var abs = require( './abs.js' );


// EXPORTS //

module.exports = abs;
